import React, {useState, useEffect} from 'react';
import Header from "./Header";
import TodoList from "./TodosList";
import InputTodo from "./InputTodo";
import { v4 as uuidv4 } from "uuid";
// import routing components
import {Route, Switch} from "react-router-dom";
import About from "../pages/About";
import NotMatch from "../pages/NotMatch";
import Navbar from "./Navbar";

const TodoContainer = (props) =>{
  const [todos, setTodos] = useState(getInitialTodos());

  const handleChange = (id) => {
    setTodos(prevState =>
      prevState.map(todo => {        /* il nome dell'oggetto nel metodo setState può essere qualsiasi,
                                                      come anche la proprieta del metodo map, infatti nel metodo map
                                                      che ho usato nel file todosList ho usato un'altro nome
                                                      Invece in this.state.todos.map(), todos si riferisce alla proprieta
                                                      todos dell'oggetto state qui sopra*/

        if(todo.id === id){
          return {
            ...todo,
            completed: !todo.completed,
          }
        }
        return todo;
      }),
    )
  };

  const delTodo = id => {
    setTodos([
        ...todos.filter(todo => {
          return todo.id !== id;
        }),
    ]);
  };

  const addTodoItem = title => {
    const newTodo = {
      id: uuidv4(),
      title: title,
      completed: false,
    };
    setTodos([ ...todos, newTodo ]);
  };

  const setUpdate = (updatedTitle, id) => {
    setTodos(
      todos.map( todo => {
        if (todo.id === id){
          todo.title = updatedTitle;
        }
        return todo;
      }),
    );
  };

  function getInitialTodos(){
    // getting stored items                             Questa funzione è un'alternativa a useEffect e inserita nella variablie useState per evitare che venga lanciato 2 volte
    const temp = localStorage.getItem("todos");
    const savedTodos = JSON.parse(temp);
    if(savedTodos){console.log("effect load");}else{console.log("nothing to load");}
    return savedTodos || [];
  };


  useEffect(() => {
    console.log("effect save");
    //storing todos items
    const temp = JSON.stringify(todos);
    localStorage.setItem('todos', temp);
  }, [todos]);


  return(
    <>
      <Navbar />
      <Switch>
        <Route exact path="/">
          <div className="container">
            <div className="inner">
              <Header />
              <InputTodo addTodoProps={addTodoItem} />
              <TodoList
                todos={todos}
                handleChangeProps={handleChange}
                deleteTodoProps={delTodo}
                setUpdate={setUpdate}
              />
            </div>
          </div>
        </Route>
        <Route path="/about" >
          <About />
        </Route>
        <Route path="*">
          <NotMatch />
        </Route>
      </Switch>
    </>

  );

};

export default TodoContainer;
